div.profile-groups-page .profile-groups-banner {
  background: #011627;
  color: #f6f7f8;
  height: 35vh;
  display: flex;
  flex-wrap: wrap;
  padding: 10px;
  text-align: center;
  justify-content: center;
  align-items: center;
}

div.profile-groups-container {
  display: flex;
  flex-direction: column;
  align-items: center;
}

div.profile-groups-page .profile-group {
  border: 2px solid #FBB13C;
  border-radius: 10px;
  width: 90vw;
  margin-top: 20px;
  display: flex;
  text-align: center;
  align-items: center;
  justify-content: center;
  height: 100px;
}

div.profile-groups-page .profile-group a {
  text-decoration: none;
  color: #011627;
}

div.profile-groups-button-container {
  display: flex;
  justify-content: space-between;
  margin-bottom: 70px;
  width: 90vw;
}

div.profile-groups-button-container a {
  text-decoration: none;
  margin: 20px 0;
  height: 60px;
  border: 1px solid #FF3366;
  background-color: #FF3366;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 43vw;
  border-radius: 10px;
  color: white;
}